.standards-detail {
  font-family: 'Poppins', sans-serif;
}

.standards-hero {
  position: relative;
  height: 400px;
  background-size: cover;
  background-position: center;
}

.hero-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
}

.hero-content {
  position: relative;
  z-index: 2;
  text-align: center;
  color: white;
  padding: 50px;
}

.standards-content {
  padding: 50px;
}

.standards-content h2 {
  font-size: 2rem;
  margin-bottom: 20px;
}

.standards-content p {
  font-size: 1.2rem;
  margin-bottom: 20px;
}

.standards-content img {
  width: 100%;
  border-radius: 10px;
  margin-bottom: 30px;
}